/**
* Media queries
**/
.main-product-wrapper .product h3 {
  letter-spacing: 4.3px;
  text-transform: uppercase;
}
@media only screen and (min-width: 1440px) {
  .main-product-wrapper .product h3 {
    margin-bottom: 19px;
  }
}
.main-product-wrapper .product h2 {
  margin-bottom: 10px;
  text-transform: capitalize;
}
@media only screen and (min-width: 1440px) {
  .main-product-wrapper .product h2 {
    font-family: var(--font-family-1);
    font-size: var(--theme-font-h2-mob-2-1);
    color: var(--clr-smoke-2);
    line-height: 44px;
  }
}
.main-product-wrapper .product p.price {
  margin-bottom: 10px;
  font-family: var(--font-family-1);
  font-size: var(--theme-font-p-mob-3);
  color: var(--clr-red);
  line-height: 30px;
}
@media only screen and (min-width: 1440px) {
  .main-product-wrapper .product p.price {
    margin-bottom: 25px;
  }
}
.main-product-wrapper .product .desc p {
  margin-bottom: 10px;
  font-family: var(--font-family-2);
  font-size: var(--theme-font-p-mob-3);
  color: var(--clr-smoke-2);
  line-height: 30px;
  letter-spacing: 0px;
}
@media only screen and (min-width: 1440px) {
  .main-product-wrapper .product .desc p {
    margin-bottom: 51px;
  }
}
.main-product-wrapper .varients {
  display: flex;
  justify-content: space-between;
  gap: 0 15px;
}
.main-product-wrapper .varients .wrapper {
  width: 100%;
}
.main-product-wrapper .varients__size, .main-product-wrapper .varients__gender {
  display: grid;
  gap: 10px;
  width: 100%;
  max-width: 300px;
  margin: 10px 0;
}
.main-product-wrapper .varients__size {
  grid-template-columns: 1fr 1fr 1fr;
}
.main-product-wrapper .varients__gender {
  grid-template-columns: 1fr;
}
.main-product-wrapper .varients p.option-title {
  font-family: var(--font-family-1);
  font-size: var(--theme-font-p-mob-3);
  color: var(--clr-smoke-2);
  line-height: 30px;
}
.main-product-wrapper .varients .button.button--outline {
  padding: 5px 0px;
  width: auto;
  border: 2px solid rgba(48, 48, 48, 0.2);
}
.main-product-wrapper .varients .button.button--outline span {
  color: var(--clr-smoke-2);
}
.main-product-wrapper .varients .button.button--outline:hover span, .main-product-wrapper .varients .button.button--outline.active span {
  color: var(--clr-white);
}
.main-product-wrapper .varients .button.button--outline:after {
  background: var(--clr-smoke-2);
}
.main-product-wrapper .varients .button.button--outline.active:after {
  transform: translate(0);
}